@import "styles/variables.scss";

.wrapper {
    width: 100%;
    height: calc(100vh - 395px);
    display: flex;
    justify-content: center;
    align-items: center;
}

.info {
    width: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.info > p {
    text-align: center;
}

.info > p:first-child {
    font-size: 46px;
    line-height: 110%;
    letter-spacing: -0.03em;
}

.info > :nth-child(2n) {
    margin-top: 24px;
}

@media #{$tablet} {
    .wrapper {
		height: calc(100vh - 540px);
	}

    .info > p:first-child {
        font-size: 32px;
        letter-spacing: -0.02em;
    }
    .info > :nth-child(2n) {
        margin-top: 16px;
    }
}
